import React from 'react';

const exampleImg = require('../images/1.jpg');

function Articles() {
    return (
        <div className="row d-flex justify-content-start ml-3 py-4">
            <div className="col-lg-7">
                <div className="row">
                    <div className="col-5 embed-responsive embed-responsive-16by9">
                        <img src={exampleImg} alt="images" className="images embed-responsive-item"></img>
                    </div>
                    <div className="col-7">
                        <h4>BY COMPLEX</h4>
                        <h3>The History of Michael Jordan's Banned Sneakers</h3>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Articles;