import React from 'react';

const logo = require('../images/logo.png');
const logoIcon = require('../images/logo-icon.jpg');
const banner = require('../images/banner.jpg');

function Nav() {
    return (
    <div>
        <nav className="row mr-auto py-3 fixed-top web">
            <div className="col-lg-2 pt-3 pl-4">
                <img src={logo} class="logo" alt="logo"></img>
            </div>
            <div className="col-lg-7 text-center">
                <div className="row ml-4 py-4 no-gutters">
                    <div className="col-1">
                        <h6>MUSIC</h6>
                    </div>
                    <div className="col-1">
                        <h6>STYLE</h6>
                    </div>
                    <div className="col-2">
                        <h6>POP CULTURE</h6>
                    </div>
                    <div className="col-1">
                        <h6>SPORTS</h6>
                    </div>
                    <div className="col-1">
                        <h6>LIFE</h6>
                    </div>
                    <div className="col-2">
                        <h6 className="active">SNEAKERS</h6>
                    </div>
                    <div className="col-1 icon">
                        <img src={logoIcon} class="logo" alt="Logo"></img>
                        <h6>SHOP</h6>
                    </div>
                    <div className="col-1 mx-1">
                        <h6>SHOWS</h6>
                    </div>
                    <div className="col-1 icon more">
                        <h6>MORE</h6>
                        <i title="" class="fa fa-chevron-down ml-1"></i>
                    </div>
                </div>
            </div>
            <div className="col-lg-3 py-4">       
                <div className="row no-gutters">
                    <div className="col px-1">
                        <i title="Search" class="fa fa-search fa-lg"></i>
                    </div>
                    <div className="col-1">
                        <span className="pl-2">|</span>
                    </div>
                    <div className="col px-1">
                        <i title="Facebook" class="fa fa-facebook-f fa-lg"></i>
                    </div>
                    <div className="col px-1">
                        <i title="Twitter" class="fa fa-twitter fa-lg"></i>
                    </div>
                    <div className="col px-1">
                        <i title="Instagram" class="fa fa-instagram fa-lg"></i>
                    </div>
                    <div className="col px-1">
                        <i title="YouTube" class="fa fa-youtube fa-lg"></i>
                    </div>
                    <div className="col px-1">
                        <i title="Snapchat" class="fa fa-snapchat-ghost fa-lg"></i>
                    </div>
                    <div className="col px-1">
                        <i title="Tik-Tok" class="fa fa-headphones fa-lg"></i>
                    </div>                 
                </div>
            </div>
        </nav>
        <div className="row hide">
            <div className="col-12">
                <div className="row d-flex justify-content-center">
                    <div className="col-8 text-center">
                        <input placeholder="Search"></input>
                    </div>
                    <div className="col-1">
                        <i title="Search" class="fa fa-search fa-lg"></i>
                    </div>
                </div>
            </div>
        </div>
        <div className="row">
            <div className="col">
                <img src={banner} alt="banner" id="banner"></img>
            </div>
        </div>
        <div className="row">
            <div className="col-lg-8 mx-auto py-4">
                <h1 className="pb-4">A Complete Guide to Alex's <br></br>Sneakers</h1>
            </div>
        </div>
        <div className="row">
            <div className="col-lg-8 mx-auto">
                <div className="row">
                    <div className="col">
                        <h5>BY <a href="https://al-byte.github.io/Portfolio-Alt/">ALEX WASHINGTON</a></h5>
                    </div>
                    <div className="col text-right">
                        <h5>May 4, 2020</h5>
                    </div>
                </div>
            </div>
        </div>
    </div>
    )
}

export default Nav;